/* Overall Section */
.cube-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 3rem 8rem;
    background: linear-gradient(to left, #ffffff, #fff6eb);
    gap: 2rem;
}

/* Left Content Section */
.cube-content {
    flex: 1;
}

.cube-heading {
    font-size: 50px;
    font-weight: 44;
    color: #343434;
    margin-bottom: 1rem;
    font-family: Lilita One;
}

.cube-description {
    font-size: 24px;
    color: #455a64;
    margin-bottom: 2rem;
    font-family: geologica;
    line-height: 1.3;
}

/* Points List */
.cube-points {
    list-style: none;
    padding: 0;
    margin-bottom: 2rem;
}

.cube-points li {
    display: flex;
    align-items: center;
    margin-bottom: 1.5rem;
    font-family: geologica;
    font-size: 24px;
    color: #343434;
}

.cube-points li img {
    margin-right: 0.8rem;
    height: 24px;
    width: 24px;
}

.cube-points li span {
    line-height: 1.6;
    font-size: 24px;
}

/* Button Style */
.cube-button {
    display: flex; 
    justify-content: center; 
    align-items: center; 
    padding: 20px 16px; 
    background-color: #f58634;
    color: white;
    border-radius: 8px;
    text-decoration: none;
    font-weight: bold;
    font-size: 19.19px;
    width: 270px; 
    height: 56px; 
    transition: background-color 0.3s ease;
    box-sizing: border-box;
    box-shadow: 0 0 10px rgba(255, 124, 46, 0.6);
}

.cube-button:hover {
    background-color: #f58634;
    box-shadow: 0 0 20px rgba(255, 124, 46, 0.8);
}

/* Right Image Section */
.cube-image {
    flex: 1;
    display: flex;
    justify-content: flex-end;
}

.cube-image img {
    width: 558px;
    height: 626px;
    border-radius: 10px;
    transition: transform 0.3s ease;
}

.cube-image img:hover {
transform: translateY(-10px);
}

@media (max-width: 374px) {

    .cube-content {
      flex: 1;
      width: 65%;
    }
    .cube-section {
      padding: 0rem 1rem;
    }
  
    .cube-heading {
      font-size: 30px;
      margin-bottom: 0rem;
    }
    
    .cube-description {
      font-size: 12px;
      margin-bottom: 0rem;
      line-height: 1.3;
    }
    
    /* Points List */
    .cube-points {
      list-style: none;
      padding: 0;
      margin-bottom: 2rem;
    }
    
    .cube-points li {
      display: flex;
      margin-bottom: 1rem;
      font-size: 4px;
    }
    
    .cube-points li img {
      margin-right: 0.2rem;
      height: 10px;
      width: 12px;
    }
    
    .cube-points li span {
      line-height: 1.4;
      font-size: 12px;
    }
    
  
    .cube-button {
      font-size: 10.19px;
      width: 222px;
      height: 25px;
    }
  
    .cube-image img {
      width: 165px;
      height: 200px;
      border-radius: 10px;
      margin-left: -60px;
    }
    .cube-image{
      display: flex;
      margin-right: 25px;
    }
  }